<template>
  <div id="home">
      <NavBar class="home-nav">
          <div slot="center"></div>
      </NavBar>
      <TabControl class="tab-control" :titles="['','','']" @tabClick='tabClick' ref="tabControl1" v-show="isTabFlxed"></TabControl>

      <Scroll class="content" 
              ref="scroll" 
              :probe-type='3' 
              @scroll="contentscroll" 
              :pull-up-load='true'
              @pullingUp='loadMore'>
        <Swiper :banners='banners' @swiperImgLoad="swiperImgLoad"></Swiper>
        <HomeRecommenView :recommends='recommends'></HomeRecommenView>
        <HomeFeatureView></HomeFeatureView>
        <TabControl :titles="['','','']" @tabClick='tabClick' ref="tabControl2"></TabControl>
        <GoodsList :goods="showGoods" />
      </Scroll>

      <BackTop  @click.native='backClick' v-show="isShowBackTop"/>
  </div>
</template>

<script>
//
import Swiper from './HomeSwiper'
import HomeRecommenView from './HomeRecommenView'
import HomeFeatureView from './HomeFeatureView'
//
import NavBar from 'components/common/navbar/NavBar'
import TabControl from 'components/content/tabControl/TabControl'
import GoodsList from 'components/content/goods/GoodsList'
import Scroll from  'components/common/scroll/Scroll'
import BackTop from  'components/common/backTop/BackTop'
import {deboundce} from 'components/common/utils.js'
//
import {
  getHomeMultidata,
  getHomeGoods
  } from 'network/home.js'

export default {
  components:{
    NavBar,
    Swiper,
    HomeRecommenView,
    HomeFeatureView,
    TabControl,
    GoodsList,
    Scroll,
    BackTop
  },
  data(){
    return {
      banners : [],
      recommends :[],
      goods:{
        pop:{page:0,list:[]},
        new:{page:0,list:[]},
        sell:{page:0,list:[]},
      },
      currenType:'pop',
      isShowBackTop:false,
      tabOffsetTop:0,
      isTabFlxed:false
    }
  },
  computed:{
    showGoods(){
      return this.goods[this.currenType].list
    }
  },
  created(){//
    this.getHomeMultidatas(),//

    this.getHomeGoodss('pop')//
    this.getHomeGoodss('new')//
    this.getHomeGoodss('sell')//
  },
  mounted(){
    //
    let refresh = deboundce(this.$refs.scroll.refresh,50)
    this.$bus.$on('itemImageLoad', () => {
      refresh()
    })
  },
  methods:{
    //

    tabClick(idx){
      switch(idx) {
        case 0:
          this.currenType = 'pop'
          break
        case 1:
          this.currenType = 'new'
          break
        case 2:
          this.currenType = 'sell'
          break
      }
      this.$refs.tabControl1.currentIndex = idx
      this.$refs.tabControl2.currentIndex = idx
    },
    backClick(){//
      this.$refs.scroll.scrollTo(0,0,500)
    },
    //
    contentscroll(position){
      //
      this.isShowBackTop = (-position.y) > 1000

      //tabControl
      this.isTabFlxed = (-position.y) > this.tabOffsetTop
    },
    //
    loadMore(){
      this.getHomeGoodss(this.currenType)
    },
    swiperImgLoad(){
      //tabtop
      this.tabOffsetTop = this.$refs.tabControl2.$el.offsetTop
    },
    //
    getHomeMultidatas(){
        getHomeMultidata().then(res => {
          this.banners = res.data.banner.list
          this.recommends = res.data.recommend.list
      })
    }
    ,
    getHomeGoodss(type){
      let page = this.goods[type].page + 1
        getHomeGoods(type,page).then(res => {
          this.goods[type].list.push(...res.data.list)
          this.goods[type].page += 1
          //
          this.$refs.scroll.finishPullUp()
        })
    }
  }
}
</script>

<style scoped>
  #home{
    position: relative;
    height: 100vh;
  }
  .home-nav{
    background: var(--color-tint);
    color: white;
  }
  .content{
    overflow: hidden;
    position: absolute;
    top:40px;
    bottom: 49px;
    left: 0;
    right: 0;
  }
  .tab-control{
    position: relative;
    z-index: 2;
    background: white;
  }
</style>
